rm[data-v-ab8dd042]{min-width:auto;width:80vw}}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Shop/Subscribe.vue"],"names":[],"mappings":"AAmQA,mBACI,qBACJ,CAEA,sBAGI,kCAAmC,CAGnC,oBAAqB,CAFrB,YAAa,CAHb,uDAA0D,CAC1D,cAAe,CAKf,YAAa,CAFb,sBAAuB,CAGvB,WACJ,CAEA,sBAGI,iBAAkB,CAGlB,iBAAkB,CAFlB,6EAC4E,CAH5E,eAAgB,CAKhB,YAAa,CANb,UAOJ,CAEA,yBACI,YACJ,CAEA,kCACI,aAAyB,CACzB,cAAe,CACf,gBAAiB,CACjB,gBAAiB,CACjB,iBACJ,CAEA,kCACI,kBACJ,CAGA,wBACI,kBAAmB,CAInB,QAAS,CADT,iBAAkB,CAQlB,kCAAiD,CATjD,UAAc,CAMd,cAAe,CACf,aAAc,CARd,4BAA8B,CAK9B,cAAe,CACf,eAAgB,CAFhB,iBAAkB,CAKlB,uBAAyB,CAEzB,UACJ,CACA,8BACI,qBACJ,CACA,iCAEI,cAAe,CADf,UAEJ,CAGA,2FAGI,iBACJ,CACA,0BAQI,0BAA2B,CAP3B,UAAc,CACd,cAAe,CAKf,WAAY,CAHZ,aAAgB,CAChB,iBAAkB,CAFlB,oBAAqB,CAQrB,uBAAwB,CALxB,UAMJ,CACA,iEAGI,UAAW,CADX,iBAEJ,CACA,iCAUI,gDAAwC,CANxC,+BAAgC,CAFhC,aAAc,CAId,UAAY,CADZ,SAAW,CAGX,8BAGJ,CACA,iEATI,kBAAmB,CAFnB,YAsBJ,CAXA,gCAUI,2CAAmC,CANnC,+BAAgC,CAFhC,aAAc,CAId,WAAY,CADZ,SAAW,CAGX,yBAGJ,CAYA,4BACI,GAEI,sBACJ,CACA,GAEI,uBACJ,CACJ,CAEA,yCACI,sBAEI,cAAkB,CADlB,UAEJ,CACJ","sourcesContent":["<template>\n\n    <Head title=\"Upgrade Account\" />\n\n    <div class=\"w-full lace-self-center flex flex-col gap-y-3 bg-orange-400\">\n        <div id=\"topDiv\" class=\"w-full bg-gray-800 text-gray-50 dark:bg-gray-800 dark:text-gray-50 p-5 mb-10\">\n\n            <!--            <Message v-if=\"userStore.showFlashMessage\" :flash=\"$page.props.flash\"/>-->\n\n            <header class=\"flex justify-between pt-4\">\n                <h1 class=\"text-3xl font-semibold pb-3\">Subscription</h1>\n            </header>\n\n                <div v-if=\"shopStore.upgradeSelection===''\" class=\"w-full flex flex-col\">\n                        <div class=\"text-xl\">Choose a subscription:</div>\n                        <div class=\"mt-2 w-fit\">\n                            <button @click.prevent=\"shopStore.upgradeMonthly()\">Premium Monthly - $25 / month</button>\n                        </div>\n                        <div class=\"mt-2 w-fit\">\n                            <button @click.prevent=\"shopStore.upgradeYearly()\">Premium Yearly - $250 / year</button>\n                        </div>\n                        <div class=\"mt-2 w-fit hidden\">\n                            <button @click.prevent=\"shopStore.upgradeForever()\">Premium Forever - $999 / one time</button>\n                        </div>\n                </div>\n\n            <div class=\"flex flex-row mt-4 px-8\">\n                <div v-if=\"shopStore.upgradeSelection!==''\" class=\"space-y-2 flex flex-col\">\n                    <div v-if=\"shopStore.upgradeSelection==='monthly'\">\n                        <label for=\"standard\" class=\"font-semibold\">Premium Monthly - $25 / month</label>\n                    </div>\n                    <div v-if=\"shopStore.upgradeSelection==='yearly'\">\n                        <label for=\"standard\" class=\"font-semibold\">Premium Yearly - $250 / year</label>\n                    </div>\n                    <div v-if=\"shopStore.upgradeSelection==='forever'\">\n                        <label for=\"standard\" class=\"font-semibold\">Premium Forever - $999 / one time</label>\n                    </div>\n                </div>\n                <div v-if=\"shopStore.upgradeSelection!==''\"\n                     class=\"px-2 text-blue-500 hover:text-blue-400 font-semibold hover:underline hover:cursor-pointer\"\n                     @click=\"shopStore.changeUpgradeSelection()\">(change)</div>\n            </div>\n\n            <div v-show=\"!shopStore.showPaymentForm\" class=\"mx-auto mt-8 px-12\">\n                <h2 class=\"mt-6 mx-auto text-xl font-semibold text-white dark:text-gray-100\">Payment form is loading...</h2>\n            </div>\n\n            <div class=\"w-full mx-auto mt-8\">\n                <h2 class=\"text-2xl font-semibold text-gray-100 dark:text-gray-100\">Payment</h2>\n\n                <form id=\"payment-form\" class=\"w-full\">\n                    <div id=\"link-authentication-element\" class=\"mb-2\">\n                        <!--Stripe.js injects the Link Authentication Element-->\n                    </div>\n                    <div id=\"payment-element\" class=\"w-full\">\n                        <!--Stripe.js injects the Payment Element-->\n                    </div>\n\n                    <button id=\"submit\"\n                            class=\"h-fit bg-blue-600 hover:bg-blue-500 text-white rounded py-2 px-4\"\n                            @click.prevent=\"submit()\">\n                        <div class=\"spinner hidden\" id=\"spinner\"></div>\n                        <span id=\"button-text\">Pay now</span>\n                    </button>\n                    <div id=\"payment-message\" class=\"hidden\"></div>\n                </form>\n\n                <!-- {/* Show any error or success messages */}-->\n                <div v-if=\"error\" id=\"payment-error\" class=\"text-red-600 font-semibold w-full my-2\"> {{error}}</div>\n                <div v-if=\"message\" id=\"payment-message\" class=\"mt-1 text-white\">{{message}}</div>\n            </div>\n\n\n        </div>\n    </div>\n\n</template>\n\n<script setup>\nimport { onMounted, onUpdated } from \"vue\";\nimport { useVideoPlayerStore } from \"@/Stores/VideoPlayerStore.js\"\nimport { useUserStore } from \"@/Stores/UserStore\";\nimport { useShopStore } from \"@/Stores/ShopStore\";\nimport { useForm } from \"@inertiajs/inertia-vue3\";\nimport { loadStripe } from '@stripe/stripe-js';\nimport Messages from \"@/Components/VideoPlayer/Chat/VideoOTTChatMessages.vue\";\nimport {Inertia} from \"@inertiajs/inertia\";\n\nlet videoPlayerStore = useVideoPlayerStore()\nlet userStore = useUserStore()\nlet shopStore = useShopStore()\n\nuserStore.currentPage = 'subscribe'\nuserStore.showFlashMessage = true;\n\nlet props = defineProps({\n    user: Object,\n    error: String,\n    intent: Object,\n    elements: {}\n})\n\nconst options = {\n    mode: 'setup',\n    currency: 'cad',\n};\n\nlet stripe\nlet elements\nlet emailAddress\n\nlet StripeAPIKey = ''\nStripeAPIKey = process.env.MIX_STRIPE_KEY\n\nonMounted(async() => {\n    videoPlayerStore.makeVideoTopRight();\n    if (userStore.isMobile) {\n        videoPlayerStore.ottClass = 'ottClose'\n        videoPlayerStore.ott = 0\n    }\n    document.getElementById(\"topDiv\").scrollIntoView()\n    shopStore.customer = props.user\n\n    stripe = await loadStripe(StripeAPIKey);\n\n    initialize()\n\n    showPaymentForm()\n\n    document\n        .querySelector(\"#payment-form\")\n        // .addEventListener(\"submit\", handleSubmit);\n\n\n});\n\nfunction showPaymentForm() {\n    shopStore.showPaymentForm = true;\n}\n\nfunction initialize() {\n\n    const clientSecret = props.intent.client_secret\n    const appearance = {\n        theme: 'stripe',\n        variables: {\n            colorPrimary: '#0570de',\n            colorBackground: '#ffffff',\n            colorBackgroundText: '#ffffff',\n            colorText: '#222222',\n            colorDanger: '#df1b41',\n            fontFamily: 'Ideal Sans, system-ui, sans-serif',\n            spacingUnit: '4px',\n        },\n        rules: {\n            '.Label': {\n                color: 'var(--colorBackgroundText)',\n            },\n        }\n    };\n\n    elements = stripe.elements({appearance, clientSecret});\n\n    // this is for linkPayments\n    // const linkAuthenticationElement = elements.create(\"linkAuthentication\");\n    // linkAuthenticationElement.mount(\"#link-authentication-element\");\n    //\n    // linkAuthenticationElement.on('change', (event) => {\n    //     emailAddress = event.value.email;\n    // });\n\n    const paymentElementOptions = {\n        layout: \"tabs\",\n    };\n\n    const paymentElement = elements.create(\"payment\", paymentElementOptions);\n    paymentElement.mount(\"#payment-element\");\n\n}\n\nasync function submit() {\n    if (shopStore.upgradeStripeId === '' || null) {\n        alert('Please select a subscription.')\n        return\n    }\n    setLoading(true);\n\n    const {setupIntent, error} = await stripe.confirmSetup({\n        elements,\n        confirmParams: {\n            // Make sure to change this to your payment completion page\n            return_url: \"https://not.tv/subscription_success\",\n            // receipt_email: emailAddress,\n        },\n        redirect: 'if_required'\n\n\n    });\n\n    // This point will only be reached if there is an immediate error when\n    // confirming the payment. Otherwise, your customer will be redirected to\n    // your `return_url`. For some payment methods like iDEAL, your customer will\n    // be redirected to an intermediate site first to authorize the payment, then\n    // redirected to the `return_url`.\n\n    if (error) {\n        if (error.type === \"card_error\" || error.type === \"validation_error\") {\n            showMessage(error.message);\n        } else {\n            showMessage(\"An unexpected error occurred.\");\n            setLoading(false);\n        }\n    } else {\n        // console.log(setupIntent)\n        let form = useForm({\n            paymentMethod: setupIntent.payment_method,\n            plan: shopStore.upgradeStripeId,\n        });\n\n        //Submit the form\n        form.post('/shop/subscribe')\n\n    }\n}\n\n\n// ------- UI helpers -------\n\nfunction showMessage(messageText) {\n    const messageContainer = document.querySelector(\"#payment-message\");\n\n    messageContainer.classList.remove(\"hidden\");\n    messageContainer.textContent = messageText;\n\n    setTimeout(function () {\n        messageContainer.classList.add(\"hidden\");\n        messageContainer.textContent = \"\";\n    }, 4000);\n}\n\n// Show a spinner on payment submission\nfunction setLoading(isLoading) {\n    if (isLoading) {\n        // Disable the button and show a spinner\n        document.querySelector(\"#submit\").disabled = true;\n        document.querySelector(\"#spinner\").classList.remove(\"hidden\");\n        document.querySelector(\"#button-text\").classList.add(\"hidden\");\n    } else {\n        document.querySelector(\"#submit\").disabled = false;\n        document.querySelector(\"#spinner\").classList.add(\"hidden\");\n        document.querySelector(\"#button-text\").classList.remove(\"hidden\");\n    }\n}\n\n</script>\n\n<style scoped>\n\n/* Variables */\n* {\n    box-sizing: border-box;\n}\n\nbody {\n    font-family: -apple-system, BlinkMacSystemFont, sans-serif;\n    font-size: 16px;\n    -webkit-font-smoothing: antialiased;\n    display: flex;\n    justify-content: center;\n    align-content: center;\n    height: 100vh;\n    width: 100vw;\n}\n\nform {\n    width: 30vw;\n    min-width: 500px;\n    align-self: center;\n    box-shadow: 0px 0px 0px 0.5px rgba(50, 50, 93, 0.1),\n    0px 2px 5px 0px rgba(50, 50, 93, 0.1), 0px 1px 1.5px 0px rgba(0, 0, 0, 0.07);\n    border-radius: 7px;\n    padding: 40px;\n}\n\n.hidden {\n    display: none;\n}\n\n#payment-message {\n    color: rgb(105, 115, 134);\n    font-size: 16px;\n    line-height: 20px;\n    padding-top: 12px;\n    text-align: center;\n}\n\n#payment-element {\n    margin-bottom: 24px;\n}\n\n/* Buttons and links */\nbutton {\n    background: #5469d4;\n    font-family: Arial, sans-serif;\n    color: #ffffff;\n    border-radius: 4px;\n    border: 0;\n    padding: 12px 16px;\n    font-size: 16px;\n    font-weight: 600;\n    cursor: pointer;\n    display: block;\n    transition: all 0.2s ease;\n    box-shadow: 0px 4px 5.5px 0px rgba(0, 0, 0, 0.07);\n    width: 100%;\n}\nbutton:hover {\n    filter: contrast(115%);\n}\nbutton:disabled {\n    opacity: 0.5;\n    cursor: default;\n}\n\n/* spinner/processing state, errors */\n.spinner,\n.spinner:before,\n.spinner:after {\n    border-radius: 50%;\n}\n.spinner {\n    color: #ffffff;\n    font-size: 22px;\n    text-indent: -99999px;\n    margin: 0px auto;\n    position: relative;\n    width: 20px;\n    height: 20px;\n    box-shadow: inset 0 0 0 2px;\n    -webkit-transform: translateZ(0);\n    -ms-transform: translateZ(0);\n    transform: translateZ(0);\n}\n.spinner:before,\n.spinner:after {\n    position: absolute;\n    content: \"\";\n}\n.spinner:before {\n    width: 10.4px;\n    height: 20.4px;\n    background: #5469d4;\n    border-radius: 20.4px 0 0 20.4px;\n    top: -0.2px;\n    left: -0.2px;\n    -webkit-transform-origin: 10.4px 10.2px;\n    transform-origin: 10.4px 10.2px;\n    -webkit-animation: loading 2s infinite ease 1.5s;\n    animation: loading 2s infinite ease 1.5s;\n}\n.spinner:after {\n    width: 10.4px;\n    height: 10.2px;\n    background: #5469d4;\n    border-radius: 0 10.2px 10.2px 0;\n    top: -0.1px;\n    left: 10.2px;\n    -webkit-transform-origin: 0px 10.2px;\n    transform-origin: 0px 10.2px;\n    -webkit-animation: loading 2s infinite ease;\n    animation: loading 2s infinite ease;\n}\n\n@-webkit-keyframes loading {\n    0% {\n        -webkit-transform: rotate(0deg);\n        transform: rotate(0deg);\n    }\n    100% {\n        -webkit-transform: rotate(360deg);\n        transform: rotate(360deg);\n    }\n}\n@keyframes loading {\n    0% {\n        -webkit-transform: rotate(0deg);\n        transform: rotate(0deg);\n    }\n    100% {\n        -webkit-transform: rotate(360deg);\n        transform: rotate(360deg);\n    }\n}\n\n@media only screen and (max-width: 600px) {\n    form {\n        width: 80vw;\n        min-width: initial;\n    }\n}\n\n</style>\n\n\n\n\n\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
